"use client";

import { useState, useEffect } from "react";
import Navbar from "@/components/Navbar";

interface BilliardTable {
  id: number;
  name: string;
  description: string | null;
  espIpAddress: string;
  gpioPin: number;
  hourlyRate: number;
  isActive: boolean;
}

// Форматирование цены в сумах
function formatPrice(amount: number): string {
  return amount.toLocaleString("ru-RU") + " сум";
}

export default function TablesPage() {
  const [tables, setTables] = useState<BilliardTable[]>([]);
  const [loading, setLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingTable, setEditingTable] = useState<BilliardTable | null>(null);
  const [formData, setFormData] = useState({
    name: "",
    description: "",
    espIpAddress: "",
    gpioPin: "2",
    hourlyRate: "130000",
  });
  const [saving, setSaving] = useState(false);

  const fetchTables = async () => {
    try {
      const response = await fetch("/api/tables");
      if (response.ok) {
        const data = await response.json();
        setTables(data);
      }
    } catch (error) {
      console.error("Error fetching tables:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchTables();
  }, []);

  const openModal = (table?: BilliardTable) => {
    if (table) {
      setEditingTable(table);
      setFormData({
        name: table.name,
        description: table.description || "",
        espIpAddress: table.espIpAddress,
        gpioPin: table.gpioPin.toString(),
        hourlyRate: table.hourlyRate.toString(),
      });
    } else {
      setEditingTable(null);
      setFormData({ name: "", description: "", espIpAddress: "", gpioPin: "2", hourlyRate: "130000" });
    }
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setEditingTable(null);
    setFormData({ name: "", description: "", espIpAddress: "", gpioPin: "2", hourlyRate: "130000" });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setSaving(true);

    try {
      const url = editingTable
        ? `/api/tables/${editingTable.id}`
        : "/api/tables";
      const method = editingTable ? "PUT" : "POST";

      const response = await fetch(url, {
        method,
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name: formData.name,
          description: formData.description || null,
          espIpAddress: formData.espIpAddress,
          gpioPin: parseInt(formData.gpioPin),
          hourlyRate: parseInt(formData.hourlyRate),
        }),
      });

      if (response.ok) {
        closeModal();
        fetchTables();
      } else {
        const error = await response.json();
        alert(error.error || "Ошибка сохранения");
      }
    } catch (error) {
      console.error("Error saving table:", error);
    } finally {
      setSaving(false);
    }
  };

  const handleDelete = async (table: BilliardTable) => {
    if (!confirm(`Удалить стол "${table.name}"?`)) return;

    try {
      const response = await fetch(`/api/tables/${table.id}`, {
        method: "DELETE",
      });

      if (response.ok) {
        fetchTables();
      }
    } catch (error) {
      console.error("Error deleting table:", error);
    }
  };

  return (
    <div className="min-h-screen bg-gray-900">
      <Navbar />

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex items-center justify-between mb-6">
          <h1 className="text-2xl font-bold text-white">Настройка столов</h1>
          <button
            onClick={() => openModal()}
            className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white font-medium rounded-lg transition"
          >
            + Добавить стол
          </button>
        </div>

        {loading ? (
          <div className="flex items-center justify-center py-20">
            <svg className="animate-spin h-10 w-10 text-green-500" viewBox="0 0 24 24">
              <circle
                className="opacity-25"
                cx="12"
                cy="12"
                r="10"
                stroke="currentColor"
                strokeWidth="4"
                fill="none"
              />
              <path
                className="opacity-75"
                fill="currentColor"
                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
              />
            </svg>
          </div>
        ) : (
          <div className="bg-gray-800 rounded-xl overflow-hidden overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b border-gray-700">
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    ID
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Название
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Цена/час
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    ESP IP
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Статус
                  </th>
                  <th className="px-6 py-4 text-right text-sm font-medium text-gray-400">
                    Действия
                  </th>
                </tr>
              </thead>
              <tbody>
                {tables.map((table) => (
                  <tr
                    key={table.id}
                    className="border-b border-gray-700/50 hover:bg-gray-700/30"
                  >
                    <td className="px-6 py-4 text-gray-300">{table.id}</td>
                    <td className="px-6 py-4 text-white font-medium">
                      {table.name}
                    </td>
                    <td className="px-6 py-4 text-yellow-400 font-medium">
                      {formatPrice(table.hourlyRate)}
                    </td>
                    <td className="px-6 py-4 text-gray-300 font-mono text-sm">
                      {table.espIpAddress}
                    </td>
                    <td className="px-6 py-4">
                      <span
                        className={`px-2 py-1 text-xs font-medium rounded-full ${
                          table.isActive
                            ? "bg-green-500/20 text-green-400"
                            : "bg-gray-500/20 text-gray-400"
                        }`}
                      >
                        {table.isActive ? "Активен" : "Неактивен"}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-right whitespace-nowrap">
                      <button
                        onClick={() => openModal(table)}
                        className="px-3 py-1.5 bg-blue-600 hover:bg-blue-700 text-white text-sm font-medium rounded-lg mr-2 transition"
                      >
                        Изменить
                      </button>
                      <button
                        onClick={() => handleDelete(table)}
                        className="px-3 py-1.5 bg-red-600 hover:bg-red-700 text-white text-sm font-medium rounded-lg transition"
                      >
                        Удалить
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            {tables.length === 0 && (
              <div className="text-center py-12 text-gray-400">
                Столы не найдены. Нажмите &quot;Добавить стол&quot; чтобы создать первый.
              </div>
            )}
          </div>
        )}
      </main>

      {/* Модалка */}
      {isModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <div
            className="absolute inset-0 bg-black/60 backdrop-blur-sm"
            onClick={closeModal}
          />
          <div className="relative bg-gray-800 rounded-2xl p-6 w-full max-w-md mx-4 shadow-2xl">
            <h2 className="text-xl font-bold text-white mb-6">
              {editingTable ? "Редактировать стол" : "Добавить стол"}
            </h2>

            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-300 mb-2">
                  Название
                </label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) =>
                    setFormData({ ...formData, name: e.target.value })
                  }
                  className="w-full px-4 py-3 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500"
                  placeholder="Стол 1"
                  required
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-300 mb-2">
                  Описание (опционально)
                </label>
                <input
                  type="text"
                  value={formData.description}
                  onChange={(e) =>
                    setFormData({ ...formData, description: e.target.value })
                  }
                  className="w-full px-4 py-3 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500"
                  placeholder="Бильярдный стол №1"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-300 mb-2">
                  Цена за час (сум)
                </label>
                <input
                  type="number"
                  value={formData.hourlyRate}
                  onChange={(e) =>
                    setFormData({ ...formData, hourlyRate: e.target.value })
                  }
                  className="w-full px-4 py-3 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500"
                  placeholder="130000"
                  min="0"
                  step="1000"
                  required
                />
                <p className="text-xs text-gray-500 mt-1">
                  Например: 130000 = 130,000 сум/час
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-300 mb-2">
                  IP адрес ESP-12F
                </label>
                <input
                  type="text"
                  value={formData.espIpAddress}
                  onChange={(e) =>
                    setFormData({ ...formData, espIpAddress: e.target.value })
                  }
                  className="w-full px-4 py-3 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500 font-mono"
                  placeholder="192.168.1.100"
                  pattern="^((25[0-5]|(2[0-4]|1\d|[1-9]|)\d)\.?\b){4}$"
                  required
                />
                <p className="text-xs text-gray-500 mt-1">
                  Локальный IP адрес ESP-12F для управления люстрой
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-300 mb-2">
                  GPIO пин
                </label>
                <input
                  type="number"
                  value={formData.gpioPin}
                  onChange={(e) =>
                    setFormData({ ...formData, gpioPin: e.target.value })
                  }
                  className="w-full px-4 py-3 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500"
                  placeholder="2"
                  min="0"
                  max="16"
                />
                <p className="text-xs text-gray-500 mt-1">
                  GPIO пин для реле (по умолчанию 2)
                </p>
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={closeModal}
                  className="flex-1 py-3 px-4 bg-gray-700 hover:bg-gray-600 text-white font-medium rounded-lg transition"
                >
                  Отмена
                </button>
                <button
                  type="submit"
                  disabled={saving}
                  className="flex-1 py-3 px-4 bg-green-600 hover:bg-green-700 disabled:bg-green-800 disabled:cursor-not-allowed text-white font-medium rounded-lg transition flex items-center justify-center"
                >
                  {saving ? (
                    <svg className="animate-spin h-5 w-5" viewBox="0 0 24 24">
                      <circle
                        className="opacity-25"
                        cx="12"
                        cy="12"
                        r="10"
                        stroke="currentColor"
                        strokeWidth="4"
                        fill="none"
                      />
                      <path
                        className="opacity-75"
                        fill="currentColor"
                        d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                      />
                    </svg>
                  ) : (
                    "Сохранить"
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
